$class: 'client';

.#{$class}{
  // set some whitespace between content and edges
  @extend %section-container;
}

.#{$class}-section-title{
  // extend from section-title
  @extend %section-title;
}

.#{$class}-image {
  max-width: 100%;
  min-height: 5rem;
}

.#{$class}-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
