// make use of the custom font 'my-resume'
@font-face{
  font-family: 'my-resume';
  src:
      url('asset/font/my-resume.ttf?mndm3t12') format('truetype'),
      url('asset/font/my-resume.woff?mndm3t12') format('woff'),
      url('asset/font/my-resume.svg?mndm3t12#my-resume') format('svg');
  font-weight: normal;
  font-style: normal;
}

// Apply the next declaration to all tags which have a class
// that starts with resume-icon- or contains resume-icon-
[class^="resume-icon-"], [class*=" resume-icon-"]{
  font-family: 'my-resume' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

// Better Font Rendering ============= * /
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

